import React, { Fragment, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Line } from 'react-chartjs-2';
import axios from 'axios';

function Profile(props) {
    const compId = props.match.params.id;

    const [company, setCompany] = useState(null);
    const [chartData, setChartData] = useState({})

    useEffect(async () => {
        let response = await axios.get(`https://esg-metric-api.herokuapp.com/api/comp/${compId}`);
        let json = await response.data;
        setCompany(json);
    }, []);

    return (
        <Fragment>
            <Link to="/carbon-closet">Back to Browse</Link>
            {company &&
                <div>
                    <h1>{company.name}</h1>
                    <h3>{company.industry}</h3>
                    <div class="chart">
                        <Line
                            data={{
                                labels: ['2015', '2016', '2017', '2018', '2019'],
                                datasets: [
                                    {
                                        label: 'Metric Tonnes',
                                        data: [company.emission2015, company.emission2016, company.emission2017, company.emission2018, company.emission2019],
                                    }
                                ]
                            }}
                            options={{
                                responsive: true,
                                title: { text: 'Emissions in the Past 5 Years', display: true }
                            }}
                        />
                    </div>
                </div>
            }
        </Fragment>
    );
}

export default Profile;