import React, { useState, Fragment } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios';
import { app } from '../../firebase';

function Form() {
    const [data, setData] = useState({
        name: '',
        industry: '',
        website: '',
        emission2015: 0,
        emission2016: 0,
        emission2017: 0,
        emission2018: 0,
        emission2019: 0,
        description: ''
    });
    const [photo, setPhoto] = useState('');
    const [image, setImage] = useState('');

    const onChange = e => {
        const { name, value } = e.target;

        setData(prevData => {
            return {
                ...prevData,
                [name]: value
            }
        });
        console.log(data);
    };

    const onChangeImage = async (e) => {
        const file = e.target.files[0];
        const storageRef = app.storage().ref();
        const fileRef = storageRef.child(file.name);
        await fileRef.put(file);
        setPhoto(await fileRef.getDownloadURL());
    }

    const onSubmit = async e => {
        e.preventDefault();

        try {
            const res = await axios.post('https://esg-metric-api.herokuapp.com/api/comp/', {
                name: data.name,
                industry: data.industry,
                website: data.website,
                emission2015: data.emission2015,
                emission2016: data.emission2016,
                emission2017: data.emission2017,
                emission2018: data.emission2018,
                emission2019: data.emission2019,
                photo: photo,
                description: data.description
            }, {
                    headers: { 'Access-Control-Allow-Origin': '*' }
                }
            );

            console.log(res.data);

            setData({
                name: '',
                industry: '',
                website: '',
                emissions: 0,
            });

            setImage('');
        } catch (err) {
        }
    };

    return (
        <Fragment>
            <div>
                <Link to="/carbon-closet/">Back to Browse</Link>
                <form onSubmit={onSubmit}>
                    <label>
                        Company Name:
                        <input type="text" name="name" onChange={onChange} />
                    </label>
                    <label>
                        Industry:
                        <input type="text" name="industry" onChange={onChange} />
                    </label>
                    <label>
                        Website:
                        <input type="text" name="website" onChange={onChange} />
                    </label>
                    <label>
                        Company Logo:
                        <input type="file" name="image" onChange={onChangeImage} />
                    </label>
                    <label>
                        2015 Emissions:
                        <input type="text" name="emission2015" onChange={onChange} />
                    </label>
                    <label>
                        2016 Emissions:
                        <input type="text" name="emission2016" onChange={onChange} />
                    </label>
                    <label>
                        2017 Emissions:
                        <input type="text" name="emission2017" onChange={onChange} />
                    </label>
                    <label>
                        2018 Emissions:
                        <input type="text" name="emission2018" onChange={onChange} />
                    </label>
                    <label>
                        2019 Emissions:
                        <input type="text" name="emission2019" onChange={onChange} />
                    </label>
                    <input type="submit" value="Submit" />
                </form>
            </div>
        </Fragment>
    )
}

export default Form;
